*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
body{
  font-family: Montserrat,sans-serif;
  font-style: normal;
  font-size: 16px;
  font-weight: 600;
  width: 100%;

}
a{
  text-decoration: none;
}
input,select,textarea{
  color: #353535;
}
input::-webkit-input-placeholder, select::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
