.container{
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}
.page-content{
  position: relative;
  margin-left: 255px;
  padding: 30px;
  display: flex;
  flex-direction: column;
}
.page-content__open-site{
  color: #5AAEFF;
  font-size: 14px;
  line-height: 17px;
  font-style: italic;
  font-weight: 600;

  align-self: flex-end;
  margin: -5px 0 15px 0;
}
.page-content__title{
  font-weight: 600;
  font-size: 24px;
  line-height: 20px;

  color: #353535;
}
.page-content__profile{
  display: flex;
  align-items: center;
  align-self: flex-end;
}
.page-content__profile span{
  color: #353535;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  margin-right: 13px;
}
.page-content__profile img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.sidebar__container{
    position: fixed;
    z-index: 5;
    width: 255px;
    height: 100%;
    background: #353535;
}
.sidebar{
    width: 100%;
}
.sidebar__logo-container{
    display: flex;
    position: relative;
    width: 255px;
    height: 135px;
}
.sidebar__logo-container a{
    width: 100%;
    height: 100%;
}
.sidebar__logo{
    position: absolute;
    width: 165px;
    height: 50px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.sidebar__item,.sidebar__item_active {
    display: flex;
    align-items: center;
    justify-content: flex-start;

}
.sidebar__item a:hover{
    color: #FCFCFC;
    background: #454545;
}
.sidebar__item_active a{
    color: #FCFCFC!important;
    border-left: 2px solid #FCFCFC;
    background: #454545;

}

.sidebar__item a,
.sidebar__item_active a{
    width: 100%;
    color: #929292;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    text-decoration: none;
    padding: 18px 56px 18px 40px;
}
.arrayField__container{
    display: flex;
    flex-wrap: wrap;
}
.arrayField__item{
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 10px 0;
}
.arrayField__addItemBtn{
    border: 1px solid #FFC822;
    border-radius: 5px;
    padding: 8px 12px;
   align-self: flex-start;
    color: #353535;
    font-size: 12px;
    margin: 10px 0;
    max-width: 500px;
}
.arrayField__addItemBtn input{
    width: 530px;
}
.arrayField__removeItemBtn{
    position: absolute;
    right: -30px;
    top: 65%;
}
.arrayField__plusItemBtn{
    position: absolute;
    right: -32px;
    top: 30%;
}
.arrayField__addItemBtn,
.arrayField__removeItemBtn,
