    cursor: pointer;
}
.upload__image-wrapper{
    margin: 15px 0;
    display: flex;
    flex-direction: column;
}
.upload__image-uploadBtn{
    position: relative;
    width: 200px;
    font-size: 14px;
    line-height: 24px;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    color: #1E90FF;
    cursor: pointer;
}
.upload__image-uploadBtn:hover{
    border-color: #1E5EDE;
}
.upload__image-uploadBtn img{


    width: 40px;
    height: 40px;
    opacity: .7;
    margin-bottom: 15px;
}
.upload__image-container{
    display: flex;
}
.upload__image-item{
    position: relative;
    width: 180px;
    height: 180px;
    border-radius: 5px;
    overflow: hidden;
}
.upload__image-item img{
    position: absolute;
    width: 100%;
    height: 100%;


}

.image-item__btn-wrapper{
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 10;
}
.image-item__btn-wrapper span{
    position: absolute;
