}
.image-item__btn-wrapper span img{
    position: absolute;
    width: 100%;
    height: 100%;
}
.update__image-item{
    width: 200px;
    font-size: 14px;
    line-height: 24px;
    margin-left: 25px;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    align-self: flex-end;
    color: #1E90FF;
    cursor: pointer;

}
















